body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

/* @font-face {
  font-family: Boogaloo-Regular;
  src: url(./Boogaloo/Boogaloo-Regular.ttf);
  font-weight: bold;
} */

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
/* #FFFF00 #FF00FF #FFF6E1 #EEB7F4 #00FFFF */
.App-header {
  background-color: #00ffff;
  background: -webkit-gradient(linear, left top, right top, from(#00ffff), color-stop(35%, #ff00ff), to(#ffff00));
  background: -webkit-linear-gradient(left, #00ffff 0%, #ff00ff 35%, #ffff00 100%);
  background: linear-gradient(90deg, #00ffff 0%, #ff00ff 35%, #ffff00 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

